// All 	SCSS 
@import 'base/variables';
@import 'base/reset';
@import 'base/functions';
@import 'base/typography';
 

/* ==============================================

1. Bg Color

============================================== */
.bg-color1{
   background-color: $bg-color1; 
}
.bg-color2{
   background-color: $bg-color2; 
}
.bg-color3{
   background-color: $bg-color3; 
}
.bg-color4{
   background-color: $bg-color4; 
}
.bg-overlay:before{
	content:'';
    position:absolute;
	left:0;
	right:0;
	top:0;
	bottom:0;
	background-color:$bg-overlay;
	opacity:0.95;
	z-index:1;
}
.social-color1{
    color:$social-color1;
}
.social-color2{
    color:$social-color2;
}
.social-color3{
    color:$social-color3;
}
.social-color4{
    color:$social-color4;
}
div[class*="-wrapper"]{
	position:relative;
	z-index:3;
}
/* ==============================================

2. Bg Image

============================================== */
.bg-image1{
   background: $bg-color1 url("../images/cutter_head_light_theme.png") no-repeat fixed center/cover; 
}

.bg-image2{
   background: $bg-color1 url("../images/bg-image1.jpg") no-repeat fixed center/cover; 
}

.bg-image3{
   background: $bg-color1 url("../images/watch-video-bg.jpg") no-repeat fixed center/cover; 
}



/* ==============================================

3. Form & Button

============================================== */
form,
fieldset {
   padding:0;
   margin:0;
}

input[type='text'],
input[type='tel'],
input[type='email'],
input[type='search'], 
input[type='password'], 
textarea[class],select {
   transition:$transition-base;
   @include form-size(
      inherit,($form-space-v + 5),($form-space-h - 10),$form-radius
  ) ;
  @include form-variant(
      inherit,transparent,$form-border, inherit,$form-change-bg,$form-change-border2
  );
}


button,
input[type='button'],
input[type='reset'],
input[type='file'],
input[type='submit'] {
  transition:$transition-base;
  @include form-size(
      $form-size,$form-space-v,$form-space-h,$form-radius
  ) ;
  @include form-variant(
      $form-color,$form-bg2,$form-border2, $form-change-color2,$form-change-bg3,$form-change-border2
  );
  
}


/* ==============================================

4. Section & Inner Title

============================================== */
.section-title{ 
    position: relative;
    bottom: 9px;
	h2{
		margin-bottom:$inner-30;
		font-weight:$font-weight-semibold;
		
		@include laptop{
		    margin-bottom:20px;
		}
		@include tablet{
		    margin-bottom:20px;
		}
	}
	
	@include desktop{
	  bottom:5px;
	}@include laptop{
	  bottom:2px;
	}
	@include tablet{
	  bottom:0px;
	}
}
.inner-title{ 
    margin-bottom:$inner-30 - 10px;
	h3,h4,h5,h6{
	   margin:0;
	}
}


/* ==============================================

5. Shape  Animation

============================================== */
.cutter-ani{
	.shape-one{
		position: absolute;
    left: 9%;
    top: 28%;
    animation: animationFramesOne 25s alternate infinite linear;
	}
	.shape-two{
		    position: absolute;
    left: 38%;
    bottom: 25%;
    animation: animationFramesTwo 35s alternate infinite linear;
	}
	.shape-tree{
		    position: absolute;
    bottom: 60%;
    right: 10%;
    animation: animationFramesFour  20s infinite linear;
	}	
	.shape-four{
	 position: absolute;
        bottom: 10%;
    right: 40%;
    animation: rotated  20s infinite linear;
	}
	.shape-five{
	 position: absolute;
    bottom: 20%;
    left: 10%;
    animation: animationFramesFive  20s infinite linear;
	}
	.shape-six{
	 position: absolute;
    animation: rotated  50s infinite linear;
	}
	@include tablet{
	   display:none;
	}
}
