.play-btn {
  padding:29px 10px;
  display: block;
  text-align:center;
  width: 90px;
  height: 90px;
  border-radius: $round-radius;
  background: $bg-color1;
  cursor: pointer;
  box-shadow: 0 0 0 rgba(255, 255, 255, 0.8);
  animation: play-btn 1.5s infinite;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
	margin: auto;
  i{
	  font-size:32px;
  }
  &:hover {
    -webkit-animation: none;
    animation: none;
  }
}

.play-btn {
	&.disabled {
		pointer-events: none;
		cursor: default;
	}
}


@keyframes play-btn {
  0% {
    box-shadow: 0 0 0 0 rgba(255, 255, 255, 0.4);
  }

  70% {
    box-shadow: 0 0 0 15px rgba(255, 255, 255, 0);
  }

  100% {
