		    margin:0;
			font-size:18px;
			color:$font-color2;
			line-height: 24px;
			font-weight:$font-weight-semibold;
			span{
			   display:block;
			   color:$font-color-base;
			   font-weight:$font-weight-normal;
			   font-size:16px;
			}
		 }
		 
		 @include laptop{
		     padding: 5px 10px;
		 }
	  
	  }
   }
}
.sidebar-widget{
   margin-bottom:$inner-30;
   
   @include tablet{
       margin-bottom:$inner-30;
   }
   @include laptop{
       margin-bottom:$inner-30;
   }
   
}
.sidebar-right{
    @include tablet{
	   margin-top:$inner-60;
	}
}
.sidebar-left{
   @include tablet{
	   margin-bottom:$inner-60;
	}
}
