/* ==============================================

19. Footer Area

============================================== */

.scrollToTop {
    margin: 0 15px 15px 0;
    text-align: center;
    text-decoration: none;
    position: fixed;
    bottom: 0;
    background: $bg-color1;
    right: 0;
    width: 40px;
    height: 40px;
    border-radius: 50px;
    text-align: center;
    line-height: 40px;
	box-shadow:$box-shadow;
	z-index:111;
}

.scrollToTop:hover {
  text-decoration: none;
}

.footer-wrapper {
	overflow: hidden;
}

.footer-area{
    margin-top:210px;
	&:before{
    content: "";
    height: 700px;
    border-top-left-radius: 270px;
    border-top-right-radius: 160px;
    background: $bg-color2;
    display: block;
    position: absolute;
    transform: skewY(6deg);
	left: -50px;
    right: -70px;
    top:-130px;
	z-index:2;
	@include desktop{
	    border-top-left-radius: 130px;
        border-top-right-radius: 210px;
		transform: rotate(3deg);
		top: -106px;
	}
	@include laptop{
	   display:none;
	}
	@include tablet{
	  display:none;
	}
  }
  @include desktop{
    
		margin-top: 133px;
  }  
  @include tablet{
     margin-top:0;
	 padding-top:$inner-90;
  } 
  @include laptop{
     margin-top:0;
	 padding-top:$inner-90;
  }
}
.footer-widget-wrapper{
    padding-bottom:$inner-60;
	
	@include laptop{
		padding-bottom:$inner-40;
	}
	@include tablet{
		padding-bottom:$inner-40;
	}
}
.footer-logo{
	margin-bottom:15px;
	display:block;
	
	img{
	    position:relative;
		z-index:3;
	}
}
.footer-widget{
    margin-bottom:$inner-30;
	position:relative;
	z-index:2;
    .widget-title{
		margin-bottom:10px;
		h5{
			margin:0;
			color:rgba($font-color4, 0.9);
		}
	}
	p{
		color:rgba($font-color4, 0.9);
	}
	.widget-list{
		margin:0;
		li{
			margin-bottom:5px;
		}
		li a{
			color:rgba($font-color4, 0.9);
			
			&:hover{
			    text-decoration:underline;
			} 
		}
	}
    .social-link{
		li{
			border-top:1px solid transparent;
		}
		li a{
			font-size:20px;
			color:rgba($font-color4, 0.9);
			
			&:hover{
			  color:rgba($font-color4, 1);
		   }
		}
		li:hover{
			border-top:$form-border2;
		}
	}
	form{
		@include placeholder {
		color: $font-color4;
	}
		input{
			 @include form-size(
				  $form-size,$form-space-v,$form-space-h - 15,$form-radius
			  ) ;
			@include form-variant(
				  $form-color,$form-bg2,$form-border2, $form-color,$form-bg2,$form-border2
			  );
		}
		button{
			@include form-variant(
				  $form-color2,$form-bg3,$form-border2, $form-change-color,transparent,$form-change-border
			  );
			   @include form-size(
				  $form-size,$form-space-v,$form-space-h,$form-radius
			  ) ;
		}
	}
}

.footer-copyright-wrapper{
    padding-bottom:30px;
    position:relative;
	z-index:2;
	p,a{
		margin:0;
		color:rgba($font-color4, 0.9);
	}
	a:hover{
	   &:hover{
		  color:rgba($font-color4, 1);
	   }
	}
}

.footer-shape{
    position:absolute;
	width:100%;
	.left{
		position:absolute;
		bottom:-40px;
		left:-40px;
		
		img{
		    z-index:2;
			position:relative;
			@include desktop{
				max-width:300px;
			}@include laptop{
				max-width:300px;
			}
		}
	}
	.right{
		position: absolute;
		right: -90px;
		bottom: -120px;
		img{
		    z-index:2;
			position:relative;
			@include desktop{
				max-width:300px;
			}@include laptop{
				max-width:300px;
			}
		}
	}
	@include tablet{
	   display:none;
	}
}

.list-inline-item {
	color: $font-color2;
	font-weight: 600;
}
.blog-author-tags {
	a {
		color: $placeholder-color;
		text-transform: capitalize;
		margin: 0 5px;
		transition: all .4s linear 0s;
		&:hover {
			color: $bg-color2;
		}
	}
}
.footer-widget {
	z-index: 5;
}



.swiper-slide-duplicate-prev img {
    border: 11px solid $bg-color1;
}
.swiper-slide-duplicate-prev  {
    border: 11px solid $bg-color1;
}




